
.column-header {
  cursor: pointer;
  background-color: #f2f2f2;
  padding: 8px 12px;
  text-align: left;
}

.column-header:hover {
  background-color: #e0e0e0;
}

.column-header:active {
  background-color: #c0c0c0;
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left: 4px solid #767676;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.side-bar-card  {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  /* height: 140px; */
  background: #FFFFFF;
  box-shadow: 0 0 6px #4C95F650;
  border-radius: 6px;
  opacity: 1;
  padding: 10px;
  cursor: pointer;
  text-align: left;

  .card-body {
    padding-top: 1px;
  }
}


.side-bar-card-custom {
  width: 320px;
  height: 200px !important;
  float: left;
  text-align: left !important;
}

.new-card-width {
  width: 320px !important;
}

.card-align{
  display:flex;
  flex-direction: row;
  justify-content:flex-start;
  align-items: center;
}

.button-18 {
  align-items: center;
  background-color: #0A66C2;
  border: 0;
  border-radius: 100px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  font-family: -apple-system, system-ui, system-ui, "Segoe UI", Roboto, "Helvetica Neue", "Fira Sans", Ubuntu, Oxygen, "Oxygen Sans", Cantarell, "Droid Sans", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Lucida Grande", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 20px;
  max-width: 480px;
  min-height: 40px;
  min-width: 0px;
  overflow: hidden;
  padding: 0px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  touch-action: manipulation;
  transition: background-color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s, box-shadow 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s, color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
}

.button-18:hover,
.button-18:focus { 
  background-color: #16437E;
  color: #ffffff;
}

.button-18:active {
  background: #09223b;
  color: rgb(255, 255, 255, .7);
}

.button-18:disabled { 
  /* cursor: not-allowed; */
  background: rgba(0, 0, 0, .08);
  color: rgba(0, 0, 0, .3);
}

